@import url('https://at.alicdn.com/t/font_736321_zd60ffkgo2p.css');

.iconfont {
  display: inline-block;
  font-style: normal;
  vertical-align: baseline;
  text-align: center;
  text-transform: none;
  line-height: 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

.vov {
  position: relative;
  display: flex;
  justify-content: center;
  height: 100%;

  .picture-list > div {
    height: 60%;
  }

  .LazyLoad {
    height: 100% !important;
  }

  b, strong {
    font-weight: bold;
  }
}



